/**
 * Layout component that queries for data
 * with Gatsby's useStaticQuery component
 *
 * See: https://www.gatsbyjs.com/docs/use-static-query/
 */

import * as React from "react"
import PropTypes from "prop-types"
import { useStaticQuery, graphql } from "gatsby"

import Header from "./header"
import { Box, Grommet, Button } from 'grommet';

import { deepFreeze } from "grommet/utils"

const theme = {
  "global": {
    "colors": {
      "brand": "#c09eff",
      "focus": "#9d69ff",
      "selected": "focus",
      "accent-1": "#91dcff",
      "accent-2": "#a6ffcf",
      "accent-3": "#ffeab0",
      "accent-4": "#b0b1ff",
      "neutral-1": "#6fadc9",
      "neutral-2": "#7abf9a",
      "neutral-3": "#c7b687",
      "neutral-4": "#7f7fba",
      "status-critical": "#ffa6a6",
      "status-error": "#e6a6ff",
      "status-warning": "accent-3",
      "status-ok": "accent-2"
    }
  },
  "button": {
    "border": {
      "radius": "2px"
    },
    "fill": false,
    "padding": {
      "vertical": "2px",
      "horizontal": "5px"
    }
  }
}


const Layout = ({ children }) => {
  const data = useStaticQuery(graphql`
    query SiteTitleQuery {
      site {
        siteMetadata {
          title
        }
      }
    }
  `)

  return (
    <Grommet theme={theme}>
      <Box
        tag='header'
        direction='row'
        align='left'
        justify='between'
        background='light-2'
        height='xxsmall'
        elevation='medium'
      >
        <h4>
          {data.site.siteMetadata?.title || `Title`}
        </h4>
        <Box direction='row' pad='small' gap='small'>
          <Button color='brand' label="Dashboard" />
          <Button color='brand' label="Stats" />
          <Button color='brand' label="About" />
        </Box>
      </Box>
      <div
        style={{
          margin: `0 auto`,
          maxWidth: 960,
          padding: `0 1.0875rem 1.45rem`,
        }}
      >
        <main>{children}</main>
        <footer
          style={{
            marginTop: `2rem`,
          }}
        >
           {new Date().getFullYear()}, Built with
          {` `}
          <a href="https://www.gatsbyjs.com">Gatsby</a>
        </footer>
      </div>
    </Grommet>
  )
}

Layout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default Layout
